'use client'
import { motion } from 'framer-motion'
import Image from 'next/image'

export default function SectionSeparator() {
  return (
    <motion.div
      className="relative w-full h-32 md:h-48 my-8" // Altura da nuvem
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 1.5 }}
      viewport={{ once: true, amount: 0.5 }}
    >
      <Image
        src="/images/nuvens/n2.svg"
        alt="Separador de fumaça"
        layout="fill"
        objectFit="cover" // Estica para preencher a div
        className="opacity-50" // Deixa a fumaça sutil
      />
    </motion.div>
  )
}